<template lang="html">
<div class="notify" :id="id"   @mousemove="move"  @mouseup="moveEnd">
    <div class="notify-mask"></div>
    <div class="notify-p-main" @click="close">
        <div :id="id + '_alert'" class="notify-main notify-alert" v-if="type == 0" :style="{left:offset[0] + 'px',top:offset[1] +'px', margin:offset[2]}">
            <h2 class="title" @mousedown="moveStart">{{title}}</h2>
            <div class="notify-content">
                {{content}}
            </div>
            <div class="notify-btns">
                <pzbutton btn="primary" @click.native="btnyes" size="small">确定</pzbutton>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import pzbutton from '../../button/index';
export default {
    data() {
            return {
                id: '',
                type: 0, //0（信息框，默认）1（页面层）2（iframe层）3（加载层）4（tips层）
                title: '信息',
                content: '',
                area: 'auto',
                offset: 'auto',
                icon: -1,
                btn: '确定',
                time: 0,
                shade: true,
                yes: '',
                cancel: '',
                moveLeft: 0, //左移的距离
                moveTop: 0, //上移的距离
                ismove: false
            }
        },
        computed: {},
        mounted() {},
        methods: {
            'close': function(event) {
                let mask = event.target.getAttribute("class");
                if (mask && mask.indexOf("notify-p-main") > -1) {
                     delete this.$layer.instances[this.id];
                    document.getElementById(this.id).remove();
                }
            },
            'btnyes': function(event) {
                if (typeof(this.yes) == "function") {
                    this.yes();
                } else {
                    delete this.$layer.instances[this.id];
                    document.getElementById(this.id).remove();
                }
            },
            'moveStart': function(event) {
                this.offset = this.offset == 'auto' ? [] : this.offset;
                if (this.offset.length == 0) {
                    this.offset.push(document.getElementById(this.id + "_alert").offsetLeft);
                    this.offset.push(document.getElementById(this.id + "_alert").offsetTop);
                    this.offset.push(0);
                }
                if(this.offset.length == 2) {
                  this.offset.push(0);
                }
                this.offset[0]= (document.getElementById(this.id + "_alert").offsetLeft);
                this.offset[1]=(document.getElementById(this.id + "_alert").offsetTop);
                this.moveLeft = event.clientX;
                this.moveTop = event.clientY;
                this.ismove = true;
            },
            'move': function(event) {
                if (this.ismove) {
                  let o = document.getElementById(this.id + "_alert");
                  o.style.left = this.offset[0] + (event.clientX - this.moveLeft) + "px";
                  o.style.top = this.offset[1] + (event.clientY - this.moveTop) + "px";
                }
            },
            'moveEnd': function(event) {
                this.ismove = false;
            }
        },
        watch:{

        },
        components: {
            pzbutton,
        }
}

</script>



